/* CSS */
html {

    padding: 0px;
    
}
div {
    border: 0px dotted lightgray;
    
}
.grid{
    display: grid;
    border: 0px;
    grid-template-rows: 0.1fr 1fr 0.01fr;
    grid-template-columns: 0.01fr 1fr 1fr;
    height: 100vh;
    overflow-Y:hidden;
    background-color: rgb(244, 244, 244);
    
}



.navbar{
    position: relative;
    grid-column: 1 /span 3;
    border: 0px solid green;
    height: 100%;
}

.navgrid{
    display: grid;
    border: 0px solid rgb(55, 255, 0);
    grid-template-rows: 0.2fr 1fr 0.2fr;
    grid-template-columns:repeat(7, 1fr) 1.3fr;
    height: 100%;
    background-color: rgb(255, 255, 255);
    
}
.navgrid > div{
    border: 0px dotted blue;
    
}

.leftcolumn{
    width: 100px;    
    grid-row: 2 / span 1;
    border-top: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    transition: width 2s;
    background-color: rgb(255, 255, 255);
  
}
.leftcolumnnarrow{
    width: 21px;   
    border: 1px solid lightgray;
    transition: width 2s;
    
    
}
.rightarea{
    margin: 20px;
    padding: 10px;
    grid-column: 2 /span 2;
    border: 0px solid red;
    border-radius: 10px 40px 0px 10px;
    overflow-y: scroll;    
    background-color: rgb(255, 255, 255);
}
.leftarrow{
    display: inline;
    float: right;
    border:2px outset lightgray;
    background-color: lightgray;
   &:hover{
        cursor:pointer;
    }
    color:black;
}

.toparrow{
    display: inline;
    float: right;
    border:2px outset lightgray;
    background-color: lightgray;
    &:hover{
        cursor:pointer;
    }
}
.footer{
    grid-column: 1/span 3;
    background-color: black;
    height: 20em;
    color: white;
    border: 0px dotted yellowgreen;
    transition: height 1s;
    
}

.footercollapsed{
    grid-column: 1/span 3;
    background-color:black;
    height: 1.5em;
    color: white;
    transition: height 1s;
   
}

.footerup{
    display: grid;
    width: 100%;
    grid-template-rows: 1.5fr 1fr 1fr 1fr 1.5fr;
    grid-template-columns: 1.5fr 1fr 1fr 1fr 1.5fr;
    border: 0px solid red;
    background-color: black;
    transition: width 1s;    
}
.footerup > div{
  background-color: black;
  text-align: left;
}
.footertopleft{
    grid-row: 1;
    grid-column: 2;
}
.footertopmiddle{
    grid-row: 1;
    grid-column: 3;
}
.footertopright{
    grid-row: 1;
    grid-column: 4;
}.footerbottomleft{
    grid-row: 2;
    grid-column: 2;
}
.footerbottommiddle{
    grid-row: 2;
    grid-column: 3;
}
.footerbottomright{
    background-color: black;
    grid-row: 2;
    grid-column: 4;
}

.logo{
    grid-Column: 1 /span 2;
    grid-Row: 2;
    margin:auto;
    font-Family: arial;
    font-Size: 2em;
}


.profilemenu{          
    color:rgb(146, 9, 100);
    font-size: 1rem;
    height: 50px;
    background-color: rgb(255, 255, 255);
    
}

.dropdivs{
    overflow:hidden;         
    border: 1px solid lightgray;
    border-radius: 10px;
    background-color: rgb(240, 242, 240);
    padding: 6px;
    margin-Top: 5px;
    margin-Left: 10px;

}

.currentuser{   
    padding: 5px;
    font-weight: bold;
    
}
.dropdivcollapsed{
    height: 32px;
    transition: height 1s;
    
}

.dropdiv{
    height: 200px;
    transition: height 1s;
}

.dropdivsdiv {
    color:black;
    border: 1px solid lightgray ;
    border-radius: 15px;
    background-color: rgb(255, 255, 255);
    padding: 5px;
    margin: 3px;
    width: 120px;
    &:hover{        
        color: blue;
        background-color: rgb(239, 242, 239);
        cursor: pointer ;
        
    }
}
.righthand{
    display: flex;
    grid-Column: 8;
    grid-Row: 1 / span 3;
    align-items: center;
    height: 79;
    padding: 10px;
}
.bgColor{
    
    background-color:rgb(205, 255, 221);
}
.noborder{
    border: 0px;
    background-color: rgb(240, 242, 240);
    padding: 5px;
    margin: 3px;
}