body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.inthemiddle {
  left: 50%;
}

button {
  cursor: pointer;
  appearance: none;
  border: 0.2em solid #3d97f8;
  background: hsl(0 0 0/0);
  padding: 0.85em 1.5em;
  color: #3d97f8;
  border-radius: 2em;
  transition: 1s;
  &:hover,
  &:focus,
  &:active {
    background: #3d97f8;
    color: #fff;
  }
}
button:disabled {
  cursor: initial;
  color: #b6d5f6;
  border: 0.2em solid #b6d5f6;
  &:hover {
    background: #ffffff;
    color: #b6d5f6;
  }
}
.inputred {
  color: #ff0000;
}

.inputblack,
input {
  appearance: none;
  border: dotted 1px;
  background-color: #d8e7f7;
  outline: none;
  border-bottom: 0.2em solid #3d97f8;
  background: rgba(#3d97f8, 0.2);
  border-radius: 0.4em 0.4em 0.4em 0.4em;
  padding: 0.4em;
  color: #000000;
}
.lgBtnleft {
  position: relative;
  padding: 6px;
  width: 70px;
  left: -30px;
}

.lgBtnright {
  position: relative;
  padding: 6px;
  width: 130px;
  right: -30px;
}

.font07em {
  position: relative;
  font-size: 0.7em;
  left: 20px;
  border: none;
}
.loginbuttons {
  display: block;
  border: none;
  position: relative;
  top: 50%;
  left: 80%;
  transform: translate(-80%, -50%);
  width: 100%;
  padding: 0px;
}

.loginarea {
  border: 5px solid;
  border-color: #3d97f8;
  border-radius: 5%;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 450px;
  transform: translate(-50%, -50%);
  padding: 10px;
}
.loginput {
  text-align: left;
  width: 250px;
  position: relative;
  border: none;
  left: 50%;
  top: 10%;
  transform: translate(-50%, 0%);
}
.textLink {
  text-align: center;
  color: blue;
  &:hover,
  &:focus,
  &:active {
    color: #0b08ee;
    cursor: pointer;
    text-decoration: underline;
  }
}
.i {
  display: flex;
  margin: auto;
}
i:hover {
  color: #00fcb6;
  cursor: pointer;
}
.valid {
  color: green;
}

.valid:before {
  position: relative;
  left: -5px;
  content: "✔";
}

/* Add a red text color and an "x" when the requirements are wrong */
.invalid {
  color: red;
}

.invalid:before {
  position: relative;
  left: -5px;
  content: "✖";
}
.circle {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 24px;
  color: #fff;
  text-align: center;
  background: #fc7378;
}
